import emailjs from 'emailjs-com';

export function sendMail({ toEmail, imageTitle, creatorNickname, phrase, imageUrl, proveUrl, thens, errs }: any) {
  const SERVICE_ID = 'service_sxcooba'; // emailjs.com   ID
  const TEMPLATE_ID = 'template_wachdrr'; // emailjs.com   ID
  const PUBLIC_KEY = 'Zxxzo3ndUSqKmnT3J';

  const emailParams = {
      toEmail,
      imageUrl,
        creatorNickname,
        imageTitle,
        phrase,
        proveUrl,
  };

  emailjs.send(SERVICE_ID, TEMPLATE_ID, emailParams, PUBLIC_KEY).then(
    response => {
      console.log('SUCCESS!', response.status, response.text);
      thens();
    },
    error => {
      console.log('FAILED...', error);
      errs();
    },
  );
}
